@import './normalize.css';

.wf { font-family: sans-serif; line-height: 1.15; -ms-text-size-adjust: 100%; -webkit-text-size-adjust: 100%; font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif; font-size: 12px; line-height: 1.5; color: #495060; background-color: transparent; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }
.ivu-load-loop { animation: ani-load-loop 1s linear infinite; }
@keyframes ani-load-loop {
  from { transform: rotate(0); }
  50% { transform: rotate(180deg); }
  to { transform: rotate(360deg); }
}
.input-group-error-append, .input-group-error-prepend { background-color: #fff; border: 1px solid #ed3f14; }
.input-group-error-append .ivu-select-selection, .input-group-error-prepend .ivu-select-selection { background-color: inherit; border: 1px solid transparent; }
.input-group-error-prepend { border-right: 0; }
.input-group-error-append { border-left: 0; }
.ivu-breadcrumb { color: #999; font-size: 14px; }
.ivu-breadcrumb a { color: #495060; transition: color 0.2s ease-in-out; }
.ivu-breadcrumb a:hover { color: #8a8a8a; }
.ivu-breadcrumb > span:last-child { font-weight: 700; color: #495060; }
.ivu-breadcrumb > span:last-child .ivu-breadcrumb-item-separator { display: none; }
.ivu-breadcrumb-item-separator { margin: 0 8px; color: #dddee1; }
.ivu-breadcrumb-item-link > .ivu-icon + span { margin-left: 4px; }

[type=reset], [type=submit], button, html [type=button] { -webkit-appearance: button; }
[type=button]::-moz-focus-inner, [type=reset]::-moz-focus-inner, [type=submit]::-moz-focus-inner, button::-moz-focus-inner { border-style: none; padding: 0; }
[type=button]:-moz-focusring, [type=reset]:-moz-focusring, [type=submit]:-moz-focusring, button:-moz-focusring { outline: 1px dotted ButtonText; }
fieldset { border: 1px solid silver; margin: 0 2px; padding: 0.35em 0.625em 0.75em; }
legend { box-sizing: border-box; color: inherit; display: table; max-width: 100%; padding: 0; white-space: normal; }
progress { display: inline-block; vertical-align: baseline; }
textarea { overflow: auto; }
[type=checkbox], [type=radio] { box-sizing: border-box; padding: 0; }
[type=number]::-webkit-inner-spin-button, [type=number]::-webkit-outer-spin-button { height: auto; }
[type=search] { -webkit-appearance: textfield; outline-offset: -2px; }
[type=search]::-webkit-search-cancel-button, [type=search]::-webkit-search-decoration { -webkit-appearance: none; }
.wf::-webkit-file-upload-button { -webkit-appearance: button; font: inherit; }
details, menu { display: block; }
summary { display: list-item; }
canvas { display: inline-block; }
template { display: none; }
[hidden] { display: none; }
* { box-sizing: border-box; -webkit-tap-highlight-color: transparent; }
.wf:after, .wf:before { box-sizing: border-box; }
article, aside, blockquote, body, button, dd, details, div, dl, dt, fieldset, figcaption, figure, footer, form, h1, h2, h3, h4, h5, h6, header, hgroup, hr, input, legend, li, menu, nav, ol, p, section, td, textarea, th, ul { margin: 0; padding: 0; }
button, input, select, textarea { font-family: inherit; font-size: inherit; line-height: inherit; }
ol, ul { list-style: none; }
input::-ms-clear, input::-ms-reveal { display: none; }
a { color: #333; background: 0 0; text-decoration: none; border-bottom: unset !important; outline: 0; cursor: pointer; transition: color 0.2s ease; }
a:hover { color: #8a8a8a; }
a:active { color: #8a8a8a; }
a:active, a:hover { outline: 0; text-decoration: none; border-bottom: unset !important; background-image: unset !important; background-color: transparent !important; }
a[disabled] { color: #ccc; cursor: not-allowed; pointer-events: none; }
code, kbd, pre, samp { font-family: Consolas, Menlo, Courier, monospace; }
@font-face { font-family: Ionicons; src: url(fonts/ionicons.ttf?v=2.0.0) format("truetype"); font-weight: 400; font-style: normal; }
